<template>
  <div>
    <van-image round lazy-load width="1.2rem" height="1.2rem" :src="url" />
    <div class="text">{{ msg }}</div>
  </div>
</template>

<script>
export default {
  name: "Bjj",
  data() {
    return {
      msg: "不将就",
      url: require("@/assets/images/bjj.png"),
    };
  },
};
</script>

<style>
.text {
  margin-top: 5px;
  color: #646566;
  font-size: 14px;
  text-align: center;
}
</style>


