<template>
  <div>
    <van-cell is-link>
      <!-- 使用 title 插槽来自定义标题 -->
      <template #title>
        <van-tag type="danger" offset="right">标签</van-tag>
      </template>
    </van-cell>
    <van-tag type="primary">标签</van-tag>
    <van-tag type="success">标签</van-tag>
    <van-tag type="danger">标签</van-tag>
    <van-tag type="warning">标签</van-tag>
  </div>
</template>

<script>
export default {
  name: "Setting",
  data() {
    return {};
  },
};
</script>


