<template>
  <div>
    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o" to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="search" dot to="/search">查询</van-tabbar-item>
      <van-tabbar-item icon="cart-o" badge="5" to="/cart"
        >购物车</van-tabbar-item
      >
      <van-tabbar-item icon="friends-o" badge="99+" to="/bjj"
        >我的</van-tabbar-item
      >
      <van-tabbar-item icon="setting-o" badge="20" to="/setting"
        >设置</van-tabbar-item
      >
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: "Tabbar",
  data() {
    return {
      active: 0,
    };
  },
};
</script>

