<template>
  <div class="app_index">
    <!-- 顶部logo、搜索框、登录按钮 -->
    <van-row>
      <van-col>
        <img class="logo" src="@/assets/images/logo-img.png" />
      </van-col>
      <van-col>
        <van-search
          class="index_header_search"
          v-model="search_v"
          placeholder="搜索商品, 共47703款好物"
        />
      </van-col>
      <van-col>
        <div class="loginBtn">登录</div>
      </van-col>
    </van-row>

    <!-- 滚动的tab按钮列表 -->
    <van-tabs line-height="2px">
      <van-tab v-for="(n, inx) in tabBtnList" :key="inx" :title="n"> 

      </van-tab>
    </van-tabs>

    <!-- 轮播图 -->
    <van-swipe :autoplay="1000" indicator-color="white">
      <van-swipe-item v-for="(url, inx) in swipeImgs" :key="inx">
        <img :src="url" />
      </van-swipe-item>
    </van-swipe>

    <!-- 滚动条下的广告 -->
    <van-row class="service_info">
      <van-col span="8">
        <i class="icon icon_1"></i>
        <span>网易自营品牌</span>
      </van-col>
      <van-col span="8">
        <i class="icon icon_2"></i>
        <span>30天无忧退货</span>
      </van-col>
      <van-col span="8">
        <i class="icon icon_3"></i>
        <span>48小时快速退款</span>
      </van-col>
    </van-row>

    <!-- 宫格：两排，十个栏目图 -->
    <van-grid :column-num="5">
      <van-grid-item v-for="(grid, index) in GridList" :key="index">
        <img :src="grid.url"/>
        <div> {{grid.text}} </div>
      </van-grid-item>
    </van-grid>

    <!-- 商品列表 -->
    <div class="goodsListDiv">
      <h1>
        <label>新品首发</label>
        <a href="#">更多 > </a>
      </h1>
      <van-grid :column-num="3">
        <van-grid-item v-for="(grid, index) in goodsList" :key="index">
          <div class="goodsDiv">
            <img :src="grid.url"/>
          </div>
          <div> {{grid.text}} </div>
        </van-grid-item>
      </van-grid>
    </div>

    <div style="height:2rem;background:#666;font-size:.5rem;">
    </div>

    <!-- 吸低，footer -->
    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item icon="search" dot>分类</van-tabbar-item>
      <van-tabbar-item icon="friends-o" badge="5">值得买</van-tabbar-item>
      <van-tabbar-item icon="cart-o" badge="20">购物车</van-tabbar-item>
      <van-tabbar-item icon="setting-o" badge="20">个人</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "app_index",
  data() {
    return {
      active: 0,
      search_v: "",
      tabBtnList: [
        "推荐",
        "居家生活",
        "服饰鞋包",
        "美食酒水",
        "个护清洁",
        "母婴亲子",
        "运动旅行",
        "数码家电",
        "严选全球",
      ],
      swipeImgs: [
        require("@/assets/images/swipe/swipe1.jpg"),
        require("@/assets/images/swipe/swipe2.jpg"),
        require("@/assets/images/swipe/swipe3.jpg"),
        require("@/assets/images/swipe/swipe4.jpg"),
        require("@/assets/images/swipe/swipe5.jpg"),
        require("@/assets/images/swipe/swipe6.jpg"),
        require("@/assets/images/swipe/swipe7.jpg"),
        require("@/assets/images/swipe/swipe8.jpg"),
      ],
      GridList: [
        {text:'新品首发',url:require("@/assets/images/grid/grid1.png")},
        {text:'居家生活',url:require("@/assets/images/grid/grid2.png")},
        {text:'服饰鞋包',url:require("@/assets/images/grid/grid3.png")},
        {text:'美食酒水',url:require("@/assets/images/grid/grid4.png")},
        {text:'个护清洁',url:require("@/assets/images/grid/grid5.png")},
        {text:'母婴亲子',url:require("@/assets/images/grid/grid6.png")},
        {text:'运动旅行',url:require("@/assets/images/grid/grid7.png")},
        {text:'数码家电',url:require("@/assets/images/grid/grid8.png")},
        {text:'全球特色',url:require("@/assets/images/grid/grid9.png")},
        {text:'好货抄底',url:require("@/assets/images/grid/grid10.gif")},
      ],
      goodsList: [
        {text:'2020新果上市，南非碧根果仁 150克',url:require("@/assets/images/goods/goods1.jpg"),price:'46',gradientPrice:0},
        {text:'奶香浓郁四层夹心 俄罗斯冰淇淋威化饼干',url:require("@/assets/images/goods/goods2.jpg"),price:'19.9',gradientPrice:0},
        {text:'全方位防漏经期神器 柔肌感经期裤 3片/包',url:require("@/assets/images/goods/goods3.jpg"),price:'12.9',gradientPrice:0},
        {text:'2020新果上市，临安山核桃仁 150克',url:require("@/assets/images/goods/goods4.jpg"),price:'56.8',gradientPrice:0},
        {text:'零食干果杂粮收纳 全景天窗可环视储物罐',url:require("@/assets/images/goods/goods5.jpg"),price:'10.9',gradientPrice:0},
        {text:'镜片/玻璃防起雾 一次性擦拭清洁防雾湿巾',url:require("@/assets/images/goods/goods6.jpg"),price:'19.9',gradientPrice:0},
      ]
    };
  },
  created() {
    axios.get('http://localhost:5632/xxx');
  },
};
</script>

<style>
.app_index {
  font-size: 0.2rem;
}

.van-row {
  height: .7rem;
}
.logo {
  width: 1.2rem;
  display: inline-block;
  margin: 0.2rem 0 0 0.3rem;
}
.index_header_search {
  width: 3.7rem;
  border: 0;
  margin: 0.1rem 0 0 0.12rem;
  padding: 0;
  color: #666;
}
.van-search__content {
  border-radius: .05rem;
  background: #ededed;
  height: .5rem;
}
.van-search .van-cell {
  padding: 1px 8px 5px 0;
}
.loginBtn {
  margin: 0.13rem 0 0 0.12rem;
  width: 0.7rem;
  height: 0.36rem;
  color: #f00;
  line-height: 0.36rem;
  text-align: center;
  font-size: 0.22rem;
  border-radius: 0.1rem;
  border: 1px solid #f00;
}

.van-tabs {
  height: .52rem;
}
.van-tabs__nav {
  height: .52rem;
}

.van-swipe {
  height: 2.555rem;
}
/* 轮播图选项矩形样式 */
.van-swipe__indicator {
  border-radius: 0;
  width: .3rem !important;
  height: .03rem !important;
}
.van-swipe-item img {
  width: 6.4rem;
  height: 2.555rem;
}

.service_info {
  text-align: center;
  height: .65rem;
  line-height: .65rem;
}
.service_info i.icon {
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 100%;
  vertical-align: middle;
  width: .266rem;
  height: .3266rem;
}
.service_info i.icon_1 {
  background-image: url('../../assets/images/icon/icon_1.png');
}
.service_info i.icon_2 {
  background-image: url('../../assets/images/icon/icon_2.png');
}
.service_info i.icon_3 {
  background-image: url('../../assets/images/icon/icon_3.png');
}

.van-grid-item {
  height: 1.451rem;
}
.van-grid-item img {
  width: .9rem;
  height: .9rem;
  margin-bottom: .1rem;
}

.goodsListDiv {
  clear: both;
  margin: .2rem 0;
}
.goodsListDiv h1 {
  display: block;
  clear: both;
  margin: 0 .2rem;
  height: .5rem;
  line-height: .5rem;
}
.goodsListDiv h1 label {
  float: left;
  font-size: .25rem;
}
.goodsListDiv h1 a {
  float: right;
  font-size: .2rem;
  color: #333;
}
.goodsListDiv .van-grid-item {
  height: 3rem;
}
.goodsListDiv .van-grid-item__content {
  padding: 16px 5px;
}
.goodsListDiv .van-grid-item img {
  width: 1.86rem;
  height: 1.86rem;
  margin-bottom: .1rem;
}
</style>


