// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import './assets/rem'
import './assets/reset.css'

import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'

// Vant
import {
  Col,
  Row,
  Tag,
  Tab,
  Tabs,
  Icon,
  Card,
  Grid, 
  GridItem,
  Swipe,
  SwipeItem,
  Button,
  Dialog,
  Popup,
  Search,
  Cell,
  CellGroup,
  Notify,
  Image as VanImage,
  Lazyload,
  Tabbar,
  TabbarItem,
  Collapse,
  CollapseItem
} from 'vant';
import 'vant/lib/index.css';

// Element
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(Col)
  .use(Row)
  .use(Tag)
  .use(Tab)
  .use(Tabs)
  .use(Icon)
  .use(Cell)
  .use(Card)
  .use(Grid)
  .use(GridItem)
  .use(Popup)
  .use(Swipe)
  .use(Button)
  .use(Tabbar)
  .use(Search)
  .use(Dialog)
  .use(Notify)
  .use(VanImage)
  .use(Collapse)
  .use(Lazyload)
  .use(SwipeItem)
  .use(CellGroup)
  .use(TabbarItem)
  .use(CollapseItem)
  .use(VueResource)
  .use(ElementUI);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



