import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Demo/Index'
import Home from '@/components/Demo/Home/Home'
import Search from '@/components/Demo/Home/Search'
import Cart from '@/components/Demo/Home/Cart'
import Bjj from '@/components/Demo/Home/Bjj'
import Setting from '@/components/Demo/Home/Setting'
import app_index from '@/components/you/app_index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'app_index',
      component: app_index
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children: [{
          path: '/home',
          name: 'Home',
          component: Home
        },
        {
          path: '/search',
          name: 'Search',
          component: Search
        },
        {
          path: '/cart',
          name: 'Cart',
          component: Cart
        },
        {
          path: '/Bjj',
          name: 'Bjj',
          component: Bjj
        },
        {
          path: '/setting',
          name: 'Setting',
          component: Setting
        }
      ]
    }
  ]
})



