<template>
  <div>
    <van-search
      v-model="value"
      shape="round"
      background="#4fc08d"
      placeholder="请输入搜索关键词"
    />
    <van-collapse v-model="activeName" accordion>
      <van-collapse-item
        v-for="(item, index) in list"
        :key="index"
        :name="index"
      >
        <template #title>
          {{ item.empName }}
        </template>
        <van-cell
          :title="item.empNo"
          :value="item.seattype + ' | ' + item.starname"
        />
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      value: "",
      activeName: "1",
      list: [],
    };
  },
  methods: {
    getData() {
      //请求数据
      this.$http
        .post(
          `api/operator/c7/f5?currentEmpZone=7110101&sEcho=3&iColumns=9&sColumns=&iDisplayStart=10&iDisplayLength=10&mDataProp_0=%26checkbox&mDataProp_1=id&mDataProp_2=visitType&mDataProp_3=contentType&mDataProp_4=createTime&mDataProp_5=updateTime&sSearch=&bRegex=false&sSearch_0=&bRegex_0=false&bSearchable_0=true&sSearch_1=&bRegex_1=false&bSearchable_1=true&sSearch_2=&bRegex_2=false&bSearchable_2=true&sSearch_3=&bRegex_3=false&bSearchable_3=true&sSearch_4=&bRegex_4=false&bSearchable_4=true&sSearch_5=&bRegex_5=false&bSearchable_5=true`,
          {}
        )
        .then(
          (res) => {
            this.list = res.body.aaData;
          },
          (err) => {
            console.log(err);
          }
        );
    },
  },
  mounted() {
    this.getData();
  },
};
</script>


